import React from "react";
import { Text, Pressable, StyleSheet } from "react-native";
import { Audio } from "expo-av";

export const Letter = ({ sign, soundFile, style }) => {
  function onPressLetter() {
    playSound();
  }

  const [sound, setSound] = React.useState();

  async function playSound() {
    const { sound } = await Audio.Sound.createAsync(
      require(`../assets/sound/${soundFile}`)
    );
    setSound(sound);
    await sound.playAsync();
  }

  React.useEffect(() => {
    return sound
      ? () => {
          sound.unloadAsync();
        }
      : undefined;
  }, [sound]);

  console.log(styles.letter);
  return (
    <Pressable onPress={onPressLetter}>
      <Text style={[styles.letter, style]}>{sign}</Text>
    </Pressable>
  );
};

const styles = StyleSheet.create({
  letter: {
    fontSize: 400,
  },
});
